body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #F9F1F0;
  font-family: 'Playfair Display', serif; 
  font-size: 18px;
  padding-top: 1px;
}

.header{
  text-align: center;
  background-color: #FADCD9;

}

li {
  list-style-type: none;
}

/* icon in header */
.github{
  height: 50px;
  width: 50px;
  float:left;
  margin-top: 10px;
}
/* icon in header */
.linkedin{

  height: 50px;
  width: 50px;
  float:left;
  margin-left: 10px;
  margin-top: 10px;
}

h1{
  color: black;
}

h2{
  text-align: left;
}


.flex-container{
  display: flex;
  /* justify-content:center; */
  flex-direction:column;
  font-family: 'Playfair Display', serif; 
  font-size: 18px;
}

.flex-container > div{
  width: auto;
  height: auto;
}

/* About.js */
.flex-about{
  display: flex;
  justify-content: space-between;
  flex-direction:row-reverse;
}

.flex-about > div{
  width: 1000px;
  height: auto;

}

/* Responsive layout  - one column layout instead of a two-column layout */
@media (max-width: 800px) {
  .flex-container {
    flex-direction: column;
  }
} 

@media (max-width: 800px) {
  .flex-about {
    flex-direction: column;
  }
} 




